html {
  font-family: Roboto, serif;
  font-size: 12px;
  font-weight: 500;
}

body, nav, ul {
  margin: 0;
  padding: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.header__logo {
  width: 40px;
  height: 40px;
}

.navigation__list {
  list-style-type: none;
  display: flex;
}

.list__item {
  text-transform: uppercase;
}

.list__item:not(:first-child) {
  margin-left: 20px;
}

.link {
  color: #000;
  height: 60px;
  line-height: 60px;
  text-decoration: none;
  display: block;
}

.link:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  width: 37px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}
